import TripDay from './components/tripDay.js';
import TripInfo from './components/tripInfo.js';
import TripMenu from './components/menu.js';
import EditForm from './components/form.js';
import TripSort from './components/sort.js';
import Filters from './components/tripFilters.js';
import TripList from './components/trip-days-list.js';

import { render, RenderPosition } from './util.js';
import {generateEvents} from './mock/events.js';
import {sortDatesAscending, groupByDays, getDatesOfEventDays} from './util.js'


// Variables
const tripInfo = document.querySelector('.trip-main__trip-info');
const tripNav = document.querySelector('.trip-controls__trip-tabs');
const tripControls = document.querySelector('.trip-main__trip-controls');
const tripEvents = document.querySelector('.trip-events');
const tripDays = document.querySelector('.trip-days');

const COUNT_EVENTS = 10;
let counterOfDays = 1;
const events = generateEvents(COUNT_EVENTS);

const sortedEvents = sortDatesAscending(events);
const groupOfEventsByDays = groupByDays(sortedEvents);
const eventDays = getDatesOfEventDays(sortedEvents);

render(tripControls, new TripMenu().getElement(), RenderPosition.AFTERBEGIN);
render(tripControls, new Filters().getElement(), RenderPosition.BEFOREEND);

const renderEventDays = (groupOfEventsByDays, date, tripList) => {
    const clickOnEditFormBtn = () => {
        tripList.replaceChild(editForm.getElement(), tripDayComponent.getElement());
    };

    const clickOnSaveFormBtn = (evt) => {
        evt.preventDefault();
        tripList.replaceChild(tripDayComponent.getElement(), editForm.getElement());
    };

    const tripDayComponent = new TripDay(groupOfEventsByDays[date], counterOfDays);
    const editBtn = tripDayComponent.getElement().querySelector('.event__rollup-btn');

    const editForm = new EditForm(groupOfEventsByDays[date]);
    const saveFormBtn = editForm.getElement().querySelector('.event__save-btn');

    editBtn.addEventListener('click', clickOnEditFormBtn);
    saveFormBtn.addEventListener('click', clickOnSaveFormBtn);

    render(tripList, tripDayComponent.getElement(), RenderPosition.BEFOREEND);
    counterOfDays++
};

const renderTripEventsList = (groupOfEventsByDays, eventDays, counterOfDays) => {
    render(tripEvents, new TripSort().getElement(), RenderPosition.BEFOREEND);
    render(tripEvents, new TripList().getElement(), RenderPosition.BEFOREEND);

    const tripList = document.querySelector('.trip-days');

    eventDays.forEach((date) => {
        renderEventDays(groupOfEventsByDays, date, tripList);
    });


};

renderTripEventsList(groupOfEventsByDays, eventDays);











